.texts{
    text-align: center;
    position: center;
    width: 100%;
    height: 100vh;
}

.after{
    font-size: 30px;
}

.main{
    color: white;
}

.bg{
    background-image: url("img/iji.png");
    background-size: cover;
    background-position: center;
    width: 105%;
    height: 103%;
    display: block;
    position: fixed;
    top: 0;
    filter: blur(3px);
    overflow: hidden;
    margin-top: -3px;
    margin-left: -3px;
}

section {
    position: relative;
    overflow: hidden;
}


.intro {
    display: grid;
    text-align: center;
    position: absolute;
    top: 35%;
    width: 100%;
    place-items: center;
}

.desc {
    max-width: 960px;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.whoami{
    font-size: 50px;
}

.whoami span {
    font-size: 20px;
    padding-bottom: 3px;
    display: block;
    padding-left: 3px;
}

.line {
    position: relative;
    margin-top: 10px;
    margin-bottom: 10px;
    border-top: 2px solid #fff;
    border-bottom: 2px solid #fff;
}

.fterm {
    transform: translateY(10px);
}
